const tagReg = /<\/?([^<|^>]*)>/g
/**
 * string cut
 *
 * @export
 * @param {any} value
 * @param {any} limit
 * @returns
 */
export function cutStr (value, limit) {
  if (!value) {
    return ''
  }
  value = '' + value
  // remove html tag
  value = value.replace(tagReg, '')
  let strLength = 0
  const strLen = value.length
  let strCut = ''
  for (let i = 0; i < strLen; i++) {
    const a = value.charAt(i)
    strLength++
    if (encodeURI(a).length > 4) {
      // 4
      strLength += 1
    }
    strCut = strCut.concat(a)
    if (strLength > limit) {
      strCut = strCut.concat('...')
      return strCut
    }
    if (strLength === limit) {
      return strCut
    }
  }

  // 
  if (strLength < limit) {
    return value
  }
}

/**
 * simple version deep copy
 * @param {Object} obj
 */
export function deepClone(obj) {
  // obj not contain special type likeundefined,NaN,function
  return JSON.parse(JSON.stringify(obj))
}

export function jsonProp (obj) {
  // type check
  if (!obj || (typeof obj !== 'object')) {
    return obj
  }
  Object.keys(obj).forEach((key) => {
    if ((typeof obj[key]) === 'object') {
      obj[key] = JSON.stringify(obj[key])
    }
  })
  return obj
}

export function dateFormat(format){ 
	var n = parseInt(format)*1000;
	  var D = new Date(n);
	  var year = D.getFullYear();//
	
	  var month = D.getMonth()+1;//(0-11),0
	  month = month<10?('0'+month):month;
	
	  var day = D.getDate();//(1-31)
	  day = day<10?('0'+day):day;
	
	  var hours = D.getHours();//(0-23)
	  hours = hours<10?('0'+hours):hours;
	
	  var minutes = D.getMinutes();//(0-59)
	  minutes = minutes<10?('0'+minutes):minutes;
	
	  // var seconds = D.getSeconds();//(0-59)
	  // seconds = seconds<10?('0'+seconds):seconds;
	  // var week = D.getDay();//(0-6),0
	  // var weekArr = ['','','','','','',''];
	
	  var now_time = year+'-'+month+'-'+day+' '+hours+':'+minutes;
	  return now_time;
}